@font-face {
    font-family: standardText;
    src: url(./components/assets/pokemon-ds-font.ttf);
}

@font-face {
    font-family: pokemonLogo;
    src: url(./components/assets/PokemonSolidNormal-xyWR.ttf);
}

/*font by MaurizioVit  - https://www.deviantart.com/mauriziovit/art/Pokemon-Black-and-White-Version-Font-291005220*/

@font-face {
    font-family: pokemonSubLogo;
    src: url(./components/assets/pokemon_black_and_white_version_font_by_mauriziovit_d4t992c.ttf);
}

h1 {
    font-family: pokemonSubLogo;
    text-align: center;
    
}

h2 {
    font-family: standardText;
    text-align: center;
}

.pokemon-title {
    font-family: pokemonLogo;
    display: inline;
}

h3 {
    font-family: pokemonSubLogo;
    text-align: right;
}

p {
    font-family: standardText;
    text-align: right;
}

h4 {
    font-family: standardText;
    text-align: center;
}

.scoreboard {
    border-style: solid;
    border-width: 4px;
    border-color: black;

}

.card {
    background-color: pink;
    height: 240px;
    width: 200px;
}

.card-display {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    height: 720px;
    width: 800px;

}

.card-button {
    position: absolute;
    z-index: 2;
    height: 240px;
    width: 200px;
    background-color: transparent;
    outline: none;
}
